.technologies {
  padding: 120px 0 120px 0;
  background-color: #222a36;
}

.technologies__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.technologies__title {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 1.2;
  color: #ffffff;
  margin-bottom: 90px;
}

.technologies__suptitle {
  font-family: "Poppins";
  font-style: italic;
  font-weight: 400;
  font-size: 17px;
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.5);
}

.technologies__wrapper {
  display: flex;
}

.technologies__hardskills {
  background-color: #1b222d;
  width: 400px;
  padding: 30px 15px;
  color: #ffffff;
  border-radius: 40px;
  margin-right: 15px;
}

.technologies__hardskills__title {
  color: #5f62ad;
  text-align: center;
  margin-bottom: 24px;
}

.technologies__hardskills__list {
  column-count: 2;
}

.technologies__hardskills__item {
  margin-right: 40px;
}

.technologies__softskills {
  background-color: #1b222d;
  width: 400px;
  padding: 30px 15px;
  color: #ffffff;
  border-radius: 40px;
}

.technologies__softskills__title {
  color: #5f62ad;
  text-align: center;
  margin-bottom: 24px;
}

.technologies__softskills__list {
  column-count: 2;
}

.technologies__softskills__item {
  margin-right: 40px;
  margin-bottom: 18px;
}
